:root {
  --terminals-orchestrator-start-z-index: 0;
  --terminals-orchestrator-bg-color: #373737;
  --terminals-orchestrator-workspace-bg-color: #1d1d1d;
  --terminals-orchestrator-new-window-shortcut-text-color: #484848;
