  --terminals-orchestrator-tab-bg-color: #1B2427;
  --terminals-orchestrator-tab-shadow: 0 0 4px 1px #2F2F2F;
  --terminals-orchestrator-tab-active-bg-color: #1d1d1d;
  --terminals-orchestrator-tab-text-color: #7b7b7b;
  --terminals-orchestrator-tab-shortcut-text-color: #484848;
  --terminals-orchestrator-tab-active-text-color: #aeaeae;
  --terminals-orchestrator-window-shadow-color: rgba(47, 47, 47, 0.5);
  --terminals-orchestrator-window-resize-element-bg-color: rgba(250, 250, 250, 0.3);
  --terminals-orchestrator-window-border-color: #12191e; }

.overlay {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: var(--term-js-overlay-color);
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: calc(var(--terminals-orchestrator-start-z-index) + 100); }

.modal {
  background: var(--term-js-popup-bg-color);
  border: 1px solid var(--term-js-popup-border-color);
  border-radius: 0.25rem;
  color: var(--term-js-popup-text-color);
  max-width: 20rem;
  max-height: 15rem;
  width: 100%;
  display: flex;
  flex-direction: column; }

.title {
  display: flex;
  align-items: center;
  font-size: 1.15rem !important;
  border-top-left-radius: 0.25rem;
  border-top-right-radius: 0.25rem;
  background: var(--term-js-popup-header-bg-color);
  color: var(--term-js-popup-header-text-color);
  min-height: 2.5rem; }

.titleTextContainer {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  text-align: center;
  width: 100%; }

.content {
  overflow-y: auto;
  will-change: transform;
  transform: translateZ(0);
  -webkit-overflow-scrolling: touch;
  padding-top: 1rem;
  padding-bottom: 1rem;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
  flex: 1; }

.contentTextContainer {
  width: 100%;
  text-align: center;
  line-height: 1.5; }

.contentText span {
  color: var(--term-js-text-color); }

.controls {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: 1rem; }

.submit {
  display: inline-block;
  border: none;
  background: none;
  cursor: pointer;
  padding: 0;
  margin: 0;
  outline: 0;
  color: var(--term-js-text-color);
  user-select: none;
  font-size: 0.75rem !important;
  padding-left: 1rem;
  padding-right: 1rem;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  font-size: 1rem !important;
  text-align: center;
  border-radius: 0.125rem;
  min-width: 6rem;
  color: var(--term-js-button-primary-text-color);
  background: var(--term-js-button-primary-bg-color);
  margin-left: 0.5rem;
  margin-right: 0.5rem; }
  .submit:hover {
    background: var(--term-js-button-primary-bg-color-hover); }
  .submit:active {
    background: var(--term-js-button-bg-color-active); }

.cancel {
  display: inline-block;
  border: none;
  background: none;
  cursor: pointer;
  padding: 0;
  margin: 0;
  outline: 0;
  color: var(--term-js-text-color);
  user-select: none;
