  border-radius: 0.125rem;
  min-width: 6rem;
  color: var(--term-js-button-default-text-color);
  margin-left: 0.5rem;
  margin-right: 0.5rem; }
  .cancel:hover {
    background: var(--term-js-button-primary-bg-color-hover); }
  .cancel:active {
    background: var(--term-js-button-bg-color-active); }
